import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import * as mapActions from '../../actions/mapActions';
import React, {Component} from 'react';
import Grid from './parts/Grid';


class Map extends Component{
    constructor(props){
        super(props);

        let clientWidth = document.documentElement.clientWidth;
        let clientHeight = document.documentElement.clientHeight;

        const { initMapping } = props.mapActions;
        initMapping({
            1: 'cakeHalf',
            2: 'cake'
        });

        this.state = {
            countColumn: Math.floor(clientWidth / 50),
            countRows: Math.floor(clientHeight / 50),
            gridArray: [],
            test: false,

        };
    }

    componentDidMount(){
        window.addEventListener('resize', () => this.forceUpdate())
    }

    componentWillMount(){

        this.setState({
            gridArray: this.createGridArray(),
            test: true
        });
    }

    shouldComponentUpdate(nextProps){
        this.setState({
            gridArray: nextProps.map.source,
        });
        return true;
    }

    createGridArray(rows = this.state.countRows, column = this.state.countColumn){
        let array = [];
        for(let i = 0;i < rows;i++){
            array[i] = [];
            for(let j = 0;j < column;j++){
                if(i%2)
                    array[i][j] = 1;
            }
        }
        const { initMap } = this.props.mapActions;
        initMap(array, this.state.countColumn, this.state.countRows);
        return array;
    }


    clickHandle(){
        // console.log(this.props.map, 'MAP HANDLECLICK');
    }

    forceUpdate(){

        const width = Math.floor(document.documentElement.clientWidth / 50);
        const height = Math.floor(document.documentElement.clientHeight / 50);

        if( width !== this.state.countColumn || height !== this.state.countRows){
            this.setState({
                countColumn: width,
                countRows: height,
            });

            console.log('THIS STATE WIDTH', this.state.countColumn);
            const { updateMapSize } = this.props.mapActions;
            updateMapSize(width,height);
        }
    }

    render(){

        const map = this.props.map;
        // console.log('RENDER MAP',map);
        return(
            <div className="map" onClick={this.clickHandle.bind(this)} >
                <Grid
                    map={map}
                    mapActions = {this.props.mapActions}
                    tools = {this.props.tools}
                />
            </div>
        );
    }
}

function mapStateToProps (state) {
    return {
        map: state.mapReducer,
        tools: state.toolsReducer,
    }
}

function mapDispatchToProps(dispatch) {
    return{
        mapActions: bindActionCreators(mapActions, dispatch)
    };
}


export default connect(mapStateToProps, mapDispatchToProps)(Map);